.mypage_main {
  width: 570px;
  margin: auto;
  margin-top: 90px;
  animation: appear 0.6s;
}

.top {
  display: flex;
  flex-direction: row;
  padding: 30px;
  margin-left: 50px;
}

.profile_image {
  margin-right: 40px;
}

.name_and_point {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.name,
.point {
  margin: 0;
}

.name {
  margin-bottom: 8px;
  font-size: var(--font-size-big);
  font-weight: 500;
}

.point {
  color: var(--color-desc);
}

.main {
  padding: 25px 40px;
  margin-bottom: 100px;
}

.input_container {
  margin-bottom: 30px;
}

.input_title {
  margin: 16px 0 6px 0;
}

.input {
  width: 380px;
  height: 40px;
  outline: none;
  border: 1px solid var(--color-light-border);
  border-radius: 4px;
  padding: 0 5px;
  font-size: var(--font-size-normal);
}

.button_and_input {
  display: flex;
  flex-direction: row;
  margin-bottom: 16px;
  align-items: center;
  justify-content: space-between;
}

.button {
  outline: none;
  border: none;
  background-color: var(--color-theme);
  color: white;
  width: 100px;
  height: 40px;
  border-radius: 4px;
  font-size: 15px;
  cursor: pointer;
}

@keyframes appear {
  0% {
    transform: translateY(-20px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}
